import { useState, useEffect } from 'react';
import './ReminderPanel.css';
import { toast } from 'react-toastify';
import { getApiBase } from '../utils/apiEndpoints';
import confirmDialog from '../utils/confirmDialog';

const ReminderPanel = ({ serverId, onClose }) => {
  const apiBaseUrl = getApiBase();

  const [reminders, setReminders] = useState([]);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [newReminder, setNewReminder] = useState({
    title: '',
    description: '',
    remind_at: '',
    repeat: 'once',
    channel_id: '',
    mention_user_id: ''
  });
  const [channels, setChannels] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchReminders();
    fetchChannels();
  }, [serverId]);

  const fetchReminders = async () => {
    try {
      const response = await fetch(`${apiBaseUrl}/reminders/server/${serverId}/`, {
        headers: { 'Authorization': `Bearer ${localStorage.getItem('access_token')}` }
      });
      if (response.ok) {
        const data = await response.json();
        setReminders(data);
      }
    } catch (error) {
      console.error('Error:', error);
    } finally {
      setLoading(false);
    }
  };

  const fetchChannels = async () => {
    try {
      const response = await fetch(`${apiBaseUrl}/channels/server/${serverId}/`, {
        headers: { 'Authorization': `Bearer ${localStorage.getItem('access_token')}` }
      });
      if (response.ok) {
        const data = await response.json();
        setChannels(data.filter(ch => ch.type === 'text'));
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const createReminder = async () => {
    if (!newReminder.title || !newReminder.remind_at || !newReminder.channel_id) {
      toast.error('‚ùå L√ºtfen gerekli alanlarƒ± doldurun');
      return;
    }

    try {
      const response = await fetch(`${apiBaseUrl}/reminders/create/`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('access_token')}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ server_id: serverId, ...newReminder })
      });

      if (response.ok) {
        toast.success('‚úÖ Hatƒ±rlatƒ±cƒ± olu≈üturuldu');
        setShowCreateModal(false);
        fetchReminders();
        setNewReminder({ title: '', description: '', remind_at: '', repeat: 'once', channel_id: '', mention_user_id: '' });
      } else {
        toast.error('‚ùå Olu≈üturma hatasƒ±');
      }
    } catch (error) {
      toast.error('‚ùå Baƒülantƒ± hatasƒ±');
    }
  };

  const deleteReminder = async (id) => {
    if (!await confirmDialog('Hatƒ±rlatƒ±cƒ±yƒ± silmek istediƒüinize emin misiniz?')) return;
    try {
      const response = await fetch(`${apiBaseUrl}/reminders/${id}/delete/`, {
        method: 'DELETE',
        headers: { 'Authorization': `Bearer ${localStorage.getItem('access_token')}` }
      });
      if (response.ok) {
        toast.success('‚úÖ Hatƒ±rlatƒ±cƒ± silindi');
        fetchReminders();
      }
    } catch (error) {
      toast.error('‚ùå Silme hatasƒ±');
    }
  };

  const triggerNow = async (id) => {
    try {
      const response = await fetch(`${apiBaseUrl}/reminders/${id}/trigger/`, {
        method: 'POST',
        headers: { 'Authorization': `Bearer ${localStorage.getItem('access_token')}` }
      });
      if (response.ok) {
        toast.success('‚úÖ Hatƒ±rlatƒ±cƒ± tetiklendi');
      }
    } catch (error) {
      toast.error('‚ùå Tetikleme hatasƒ±');
    }
  };

  const formatTime = (time) => {
    return new Date(time).toLocaleString('tr-TR');
  };

  const getRepeatBadge = (repeat) => {
    const badges = {
      once: { text: 'Bir kez', color: '#6b7280' },
      daily: { text: 'G√ºnl√ºk', color: '#3b82f6' },
      weekly: { text: 'Haftalƒ±k', color: '#8b5cf6' },
      monthly: { text: 'Aylƒ±k', color: '#ec4899' }
    };
    return badges[repeat] || badges.once;
  };

  return (
    <div className="reminder-overlay" onClick={onClose}>
      <div className="reminder-panel" onClick={(e) => e.stopPropagation()}>
        <div className="reminder-header">
          <h2>‚è∞ Hatƒ±rlatƒ±cƒ±lar</h2>
          <button className="close-btn" onClick={onClose}>√ó</button>
        </div>

        <div className="reminder-content">
          <button className="create-reminder-btn" onClick={() => setShowCreateModal(true)}>
            + Yeni Hatƒ±rlatƒ±cƒ±
          </button>

          {loading ? (
            <div className="loading-state">
              <div className="spinner"></div>
              <p>Y√ºkleniyor...</p>
            </div>
          ) : reminders.length === 0 ? (
            <div className="empty-state">
              <span className="empty-icon">‚è∞</span>
              <p>Hen√ºz hatƒ±rlatƒ±cƒ± yok</p>
            </div>
          ) : (
            <div className="reminders-list">
              {reminders.map((reminder) => (
                <div key={reminder.id} className="reminder-card">
                  <div className="reminder-main">
                    <h3>{reminder.title}</h3>
                    {reminder.description && <p>{reminder.description}</p>}
                    <div className="reminder-meta">
                      <span>‚è∞ {formatTime(reminder.remind_at)}</span>
                      <span>üìç # {channels.find(c => c.id === reminder.channel_id)?.name}</span>
                      <span className="repeat-badge" style={{background: getRepeatBadge(reminder.repeat).color}}>
                        üîÑ {getRepeatBadge(reminder.repeat).text}
                      </span>
                    </div>
                  </div>
                  <div className="reminder-actions">
                    <button className="trigger-btn" onClick={() => triggerNow(reminder.id)}>‚ñ∂Ô∏è Tetikle</button>
                    <button className="delete-btn" onClick={() => deleteReminder(reminder.id)}>üóëÔ∏è</button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {showCreateModal && (
          <div className="create-modal-overlay" onClick={() => setShowCreateModal(false)}>
            <div className="create-modal" onClick={(e) => e.stopPropagation()}>
              <div className="modal-header">
                <h3>Yeni Hatƒ±rlatƒ±cƒ±</h3>
                <button className="close-btn" onClick={() => setShowCreateModal(false)}>√ó</button>
              </div>
              <div className="modal-body">
                <div className="form-group">
                  <label>Ba≈ülƒ±k *</label>
                  <input value={newReminder.title} onChange={(e) => setNewReminder({...newReminder, title: e.target.value})} />
                </div>
                <div className="form-group">
                  <label>A√ßƒ±klama</label>
                  <textarea value={newReminder.description} onChange={(e) => setNewReminder({...newReminder, description: e.target.value})} rows="3" />
                </div>
                <div className="form-row">
                  <div className="form-group">
                    <label>Tarih/Saat *</label>
                    <input type="datetime-local" value={newReminder.remind_at} onChange={(e) => setNewReminder({...newReminder, remind_at: e.target.value})} />
                  </div>
                  <div className="form-group">
                    <label>Tekrar</label>
                    <select value={newReminder.repeat} onChange={(e) => setNewReminder({...newReminder, repeat: e.target.value})}>
                      <option value="once">Bir kez</option>
                      <option value="daily">G√ºnl√ºk</option>
                      <option value="weekly">Haftalƒ±k</option>
                      <option value="monthly">Aylƒ±k</option>
                    </select>
                  </div>
                </div>
                <div className="form-group">
                  <label>Kanal *</label>
                  <select value={newReminder.channel_id} onChange={(e) => setNewReminder({...newReminder, channel_id: e.target.value})}>
                    <option value="">Se√ßin</option>
                    {channels.map(ch => <option key={ch.id} value={ch.id}># {ch.name}</option>)}
                  </select>
                </div>
              </div>
              <div className="modal-footer">
                <button className="cancel-btn" onClick={() => setShowCreateModal(false)}>ƒ∞ptal</button>
                <button className="submit-btn" onClick={createReminder}>‚è∞ Olu≈ütur</button>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default ReminderPanel;

